:root {
    --sidebar-color: #58b48c;
    --header-font: 'Inter', sans-serif;
    --body-font: 'Barlow', sans-serif;
    --darker-font: #408567;
}

.sidebar-grid {
    display: grid;
    height: 100vh;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    grid-template-areas: "sidebar-top-content"
                         "sidebar-middle-content"
                         "sidebar-bottom-content";
    color: white;
}

#name {
    font-family: var(--header-font);
    font-size: 28px;
}

#links-header {
    font-family: var(--header-font);
    font-size: 28px;
}

.header {
    font-family: var(--body-font);
    font-size: 19px;
    width: 300px;
    margin: 0 auto;
}

.sidebar-top-content {
    background: var(--sidebar-color);
    grid-area: sidebar-top-content;
    padding-top: 30px;

}

.border {
    border-bottom: 1px solid var(--darker-font);
    width: 90%;
   margin: 0 auto;
   margin-top: 15px;
   border-radius: 5px;
}



.sidebar-middle-content {
    background: var(--sidebar-color);
    grid-area: sidebar-middle-content;
}

.sidebar-bottom-content {
    background: var(--sidebar-color);
    grid-area: sidebar-bottom-content;
}


.top-container {
    text-align: center;
    padding-top: 10px;
}

.top-container img {
    height: 150px;
    width: 150px;
    border-radius: 100px;
}

.top-container h4 {
    width: 300px;
    font-size: 20px;
    margin: 0 auto;
}

.middle-container {
    padding-top: 10px;
    text-align: center;

}

.middle-container ul {
    text-align: left;
    list-style-position: inside;
    width: 150px;
    margin-left: 130px;
    font-size: 1.5em;

}

.middle-container li {
    padding-top: 10px;
}

.links {
    text-decoration: underline;
    color: white;
}

.links:hover {
    color: #408567;
}

.hamburger {
    background: transparent;
    border: 0;
    color: #fff;
    font-size: 30px;
    cursor: pointer;
    display: none;
}

.hamburger:focus {
    outline: none;
}

.mobile-links {
    display: none;
}

@media only screen and (max-width: 800px) {

    .sidebar-grid {
        grid-template-columns: 1fr;
        grid-template-rows: 0.5fr 1fr;
        grid-template-areas: "sidebar-top-content" "sidebar-middle-content";
        height: 85vh;
    }

    .hamburger {
        display: inline-block;
        text-align: center;
    }

    .sidebar-top-content  {
        text-align: center;
    }

   .links-group  {
      display: none;
   }

  .middle-container ul {
    margin-left: 0px;
    margin: 0 auto;
    text-align: center;

  }

  .fa-li {
    left: -0.5em;

  }

  .sidebar-bottom-content {
    display: none;
  }

 #links-header {
   display: none;
 }

 .mobile-links {
    display: block;
}

}
